export function caretNodeAndOffset(x: number, y: number): [Node, number] | undefined {
    if (document.caretPositionFromPoint) {
        const pos = document.caretPositionFromPoint(x, y);
        if (pos) {
            return [pos.offsetNode, pos.offset];
        }
    } else if (document.caretRangeFromPoint) {
        const range = document.caretRangeFromPoint(x, y);
        return [range.startContainer, range.startOffset];
    }
}

export function caretPosition(node: Node, offset: number, markdown: string): number {
    const clickedText = node.textContent;

    if (!clickedText || clickedText.length === 0) {
        // If the block is empty, put the cursor at the beginning (= end, since it's empty) of the input.
        return 0;
    }

    if (node.nextSibling === null && offset === clickedText.length) {
        // This special case handles blocks which end with a link.
        // Without the special handling, clicking at the end of a block that contains only a link would cause
        // the cursor to be put right before the closing square bracket of the link in the markdown.
        return markdown.length;
    }

    // Find all positions of the text node which received the click inside the markdown content of the block.
    const positions = [];
    let currentIndex = markdown.indexOf(clickedText, 0);
    while (currentIndex >= 0) {
        positions.push(currentIndex);
        currentIndex = markdown.indexOf(clickedText, currentIndex + clickedText.length);
    }
    // If the text that received the click occurs only once in the block's markdown content, we can be sure that
    // this one occurrence is the one the user clicked on. So we put the cursor inside that text.
    if (positions.length === 1) {
        return positions[0] + offset;
    } else {
        // If the clicked text appears more than once in the markdown content, we have to determine which of those
        // occurrences received the click. We do this by building a list of text segments that we expect to see
        // in the markdown before the clicked text.
        const before: string[] = [];
        let prev = node.previousSibling;
        while (prev) {
            // We have to expect the href of a link because it could contain the text that received the click.
            // TODO: This is is also necessary for other elements which contain more text in their markdown
            // representation than is returned by textContent, like images.
            if (prev instanceof HTMLAnchorElement) {
                before.push(prev.getAttribute('href') || '');
            }
            before.push(prev.textContent || '');
            prev = prev.previousSibling;
        }
        // Now that we have a list of text segments that we expect to see before the clicked text, we determine
        // the minimum offset that the markdown text corresponding to the clicked text must have.
        let minOffset = 0;
        for (const cur of before) {
            minOffset = markdown.indexOf(cur, minOffset) + cur.length;
        }
        // We return the first position which is after all of the text segments that we expect before.
        for (const position of positions) {
            if (position >= minOffset) {
                return position + offset;
            }
        }
        // The above loop should always return a value, but we fall back to putting cursor at the end of the input.
        return markdown.length;
    }
}
