import { Wiki } from './Model/Wiki';
import { Page } from './Model/Page';

export default class Navigation {
    public constructor(private readonly wiki: Wiki, private readonly changePage: (page: Page) => void) {}

    public goToUri(uri: string): void {
        if (uri.startsWith('./')) {
            uri = uri.substring(2);
        }
        const newPage = this.wiki.getPage(uri);
        history.pushState({ uri: newPage.getUri() }, newPage.getTitle(), uri === '/' ? '/' : newPage.getUri());
        this.changePage(newPage);
    }
}
