import { Page, InternalLink } from './Page';

export type BlockId = string;

export class Block {
    public readonly id: BlockId;
    public readonly page: Page;
    public _content: string = '';
    public level: number;
    public prev?: Block;
    public next?: Block;

    public constructor(page: Page, content: string, level: number) {
        this.page = page;
        this._content = content.trim();
        this.level = level;
        this.id = (Date.now() + Math.round(Math.random() * 1e9)).toString();
    }

    set content(value: string) {
        if (value !== this._content) {
            this._content = value;
            this.page.onBlockChange();
        }
    }

    get content(): string {
        return this._content;
    }

    public internalLinks(): InternalLink[] {
        const links = [];
        const matches = this.content.matchAll(/\[([^\]]*)\]\(\.\/([^\)]+)\)/g);
        for (const [, linkText, to] of matches) {
            links.push({
                from: this.page.getUri(),
                to,
                linkText,
                block: this,
            });
        }
        return links;
    }

    public parents(): Block[] {
        const parents: Block[] = [];
        let parent = this.parent();
        while (parent) {
            parents.unshift(parent);
            parent = parent.parent();
        }
        return parents;
    }

    public parent(): Block | undefined {
        let current = this.prev;
        while (current && current.level >= this.level) {
            current = current.prev;
        }
        return current;
    }

    public children(): Block[] {
        const children: Block[] = [];
        let current = this.next;
        while (current && current.level > this.level) {
            children.push(current);
            current = current.next;
        }
        return children;
    }

    public isEmpty(): boolean {
        return this.content.length < 1;
    }

    /**
     * Returns the new block, which contains the `after` content
     */
    public split(before: string, after: string): Block {
        this.content = before;
        const newBlock = new Block(this.page, after, this.level);
        newBlock.prev = this;
        if (this.next) {
            this.next.prev = newBlock;
            newBlock.next = this.next;
        }
        this.next = newBlock;
        return newBlock;
    }

    /**
     * Returns the removed block
     */
    public mergeWithPrev(): Block | undefined {
        if (this.prev) {
            this.prev.content = this.prev.content + this.content;
            return this.remove();
        }
    }

    /**
     * Returns the removed block
     */
    public mergeWithNext(): Block | undefined {
        if (this.next) {
            this.content = this.content + this.next.content;
            return this.next.remove();
        }
    }

    /**
     * Returns the new level
     */
    public changeLevel(change: number): number {
        if (this.prev) {
            const oldLevel = this.level;
            this.level = Math.min(10, this.prev.level + 1, Math.max(0, this.level + change));
            if (this.level !== oldLevel) {
                this.page.onBlockChange();
            }
        }
        return this.level;
    }

    public remove(): Block {
        if (this.prev) {
            this.prev.next = this.next;
        }
        if (this.next) {
            this.next.prev = this.prev;
        }
        this.page.onBlockChange();
        return this;
    }

    public setCheckbox(checked: boolean): void {
        if (this.content.startsWith('[]') && checked === true) {
            this.content = '[x]' + this.content.substring(2);
        } else if (this.content.startsWith('[x]') && checked === false) {
            this.content = '[]' + this.content.substring(3);
        }
    }
}
