import { Page, InternalLink } from './Page';

export class PageList {
    constructor(private readonly wiki: Wiki) {}

    public filterPagesByTitle(query: string): Page[] {
        return this.wiki.getPages().filter((page) => page.getTitle().toLowerCase().indexOf(query.toLowerCase()) >= 0);
    }
}

export class Wiki {
    private readonly pages: Map<string, Page> = new Map();
    private readonly linksByFrom: Map<string, InternalLink[]> = new Map();
    private readonly linksByTo: Map<string, InternalLink[]> = new Map();
    private readonly pageList = new PageList(this);

    public addPage(uri: string, markdown: string): void {
        const page = Page.fromMarkdown(this, uri, markdown);
        this.pages.set(uri, page);
        this.storeAllLinksOnPage(page);
    }

    private onChangeListeners: (() => void)[] = [];
    private callOnChange(): void {
        for (const listener of this.onChangeListeners) {
            listener();
        }
    }

    public onChange(listener: () => void): void {
        this.onChangeListeners.push(listener);
    }

    public getPage(uri: string): Page {
        if (uri === '' || uri === '/') {
            uri = new Date().toISOString().substring(0, 10);
        }
        const page = this.pages.get(uri);
        if (page) {
            return page;
        }
        return new Page(this, uri, uri, '', false);
    }

    public getPages(): Page[] {
        return [...this.pages.values()];
    }

    public getPageList(): PageList {
        return this.pageList;
    }

    public deletePage(page: Page): void {
        this.pages.delete(page.getUri());
        this.removeAllStoredLinksFromPage(page);
        this.callOnChange();
    }

    public getAllLinksTo(page: Page): InternalLink[] {
        return (
            this.linksByTo.get(page.getUri())?.map((link) => {
                if (!link.linkText) {
                    const targetPage = this.pages.get(link.from);
                    if (targetPage) {
                        return { ...link, linkText: targetPage.getTitle() };
                    } else {
                        return { ...link, linkText: link.from };
                    }
                }
                return link;
            }) ?? []
        );
    }

    private storeAllLinksOnPage(page: Page): void {
        for (const link of page.links()) {
            const storedFrom = this.linksByFrom.get(link.from);
            if (storedFrom) {
                storedFrom.push(link);
            } else {
                this.linksByFrom.set(link.from, [link]);
            }
            const storedTo = this.linksByTo.get(link.to);
            if (storedTo) {
                storedTo.push(link);
            } else {
                this.linksByTo.set(link.to, [link]);
            }
        }
    }

    private removeAllStoredLinksFromPage(page: Page): void {
        for (const link of this.linksByFrom.get(page.getUri()) ?? []) {
            const storedTo = this.linksByTo.get(link.to);
            if (storedTo) {
                this.linksByTo.set(
                    link.to,
                    storedTo.filter((x) => x !== link),
                );
            }
        }
        this.linksByFrom.delete(page.getUri());
    }

    public update(page: Page): void {
        if (this.pages.has(page.getUri())) {
            return;
        }
        page.exists = true;
        this.pages.set(page.getUri(), page);
        this.removeAllStoredLinksFromPage(page);
        this.storeAllLinksOnPage(page);
        this.callOnChange();
    }
}
