import { Wiki } from '../Model/Wiki';
import { Page } from '../Model/Page';
import { Api } from '../Api';
import Markdown from '../Markdown';
import Navigation from '../Navigation';
import Upload from '../Upload';
import { Notifications } from './Notifications';
import { Sidebar } from './Sidebar';
import { fragmentOf, htmlBuilder, replaceContent } from '../html';
import { PageView, PageViewProps, makeBlock } from './PageView';
import { Editor } from './Editor';
import { Block } from '../Model/Block';
import { caretNodeAndOffset, caretPosition } from '../caret';

interface AdditionalPagesView {
    rootElement: HTMLDivElement;
    update: (pages: Page[]) => void;
}
export function AdditionalPagesView(props: PageViewProps): AdditionalPagesView {
    const rootElement = htmlBuilder('div').addClass('more-pages').build();
    return {
        rootElement,
        update(pages): void {
            replaceContent(
                rootElement,
                fragmentOf(pages.map((page) => PageView(props, { page, mode: 'block' }).rootElement)),
            );
        },
    };
}


export function App(api: Api): DocumentFragment {
    const wiki = new Wiki();
    const markdown = new Markdown(wiki);
    const upload = new Upload(api);
    const notifications = Notifications();
    const navigation = new Navigation(wiki, showPage);
    const blockElementMap = new Map<Block, HTMLElement>();
    const editor = Editor({
        upload,
        markdown,
        pageList: wiki.getPageList(),
        blockElementMap,
        renderBlock: (block: Block) => {
            return makeBlock(
                {
                    markdown,
                    registerBlockElement,
                    onBlockClick,
                },
                block,
            );
        },
    });
    const pageViewProps: PageViewProps = {
        markdown,
        navigation,
        deletePage,
        registerBlockElement,
        onBlockClick,
    };
    const pageView = PageView(pageViewProps);
    const additionalPagesView = AdditionalPagesView(pageViewProps);
    const sidebar = Sidebar(navigation, []);

    function registerBlockElement(block: Block, element: HTMLElement): void {
        blockElementMap.set(block, element);
    }

    function onBlockClick(block: Block, ev: MouseEvent): void {
        if (ev.target instanceof HTMLAnchorElement || editor.isEditing(block)) {
            return;
        }
        if (ev.target instanceof HTMLInputElement && ev.target.type === 'checkbox') {
            block.setCheckbox(ev.target.checked);
            return;
        }
        ev.preventDefault();
        // needed for clicks on blockquotes
        ev.stopPropagation();
        const pos = caretNodeAndOffset(ev.clientX, ev.clientY);
        let position = -1;
        if (pos) {
            position = caretPosition(pos[0], pos[1], block.content);
        }
        editor.startEditing(block, position);
    }

    function savePage(page: Page): void {
        api.savePage(page)
            .then((result) => {
                if (result.success) {
                    wiki.update(page);
                    notifications.add('Success', 'success');
                } else {
                    notifications.add('Error 1', 'error');
                }
            })
            .catch((err) => {
                console.error(err);
                notifications.add('Error 2', 'error');
            });
    }

    function deletePage(page: Page): void {
        if (!window.confirm('Do you really want to delete page "' + page.getTitle() + '"?')) {
            return;
        }
        wiki.deletePage(page);
        api.deletePage(page)
            .then((result) => {
                if (result.success) {
                    notifications.add('Success', 'success');
                } else {
                    notifications.add('Error 1', 'error');
                }
            })
            .catch((err) => {
                console.error(err);
                notifications.add('Error 2', 'error');
            });
        navigation.goToUri('/');
    }

    const pagesWithChangeListeners = new Set<Page>();
    let currentPage: Page|undefined;
    function showPage(page: Page): void {
        currentPage = page;
        pageView.update({ page, mode: 'block' });
        if (!pagesWithChangeListeners.has(page)) {
            pagesWithChangeListeners.add(page);
            page.addChangeListener(() => savePage(page));
        }
        const uri = window.location.pathname.substring(1);
        if (uri === '') {
            const additionalPages = [];
            const max = 7;
            let current = page.prev(wiki);
            while (current && additionalPages.length <= max) {
                additionalPages.push(current);
                current = current.prev(wiki);
            }
            additionalPagesView.update(additionalPages);
        } else {
            additionalPagesView.update([]);
        }
    }

   
    window.onpopstate = (e: PopStateEvent): void => {
        showPage(wiki.getPage(e.state.uri));
    };

    document.addEventListener('keydown', (ev: KeyboardEvent): void => {
        if (!currentPage) {
            return;
        }
        if (ev.key === 'z' && ev.ctrlKey) {
            ev.preventDefault();
            currentPage.undo();
            pageView.update({page: currentPage, mode: 'block'});
        }
        if (ev.key === 'Z' && ev.ctrlKey) {
            ev.preventDefault();
            currentPage.redo();
            pageView.update({page: currentPage, mode: 'block'});
        }
    });

    document.addEventListener('click', (e: MouseEvent) => {
        if (e.target instanceof HTMLAnchorElement && e.target.classList.contains('internal') && !e.ctrlKey) {
            const href = e.target.getAttribute('href');
            if (href) {
                e.preventDefault();
                navigation.goToUri(href);
            }
        }
    });

    api.loadPages().then((pages) => {
        for (const item of pages) {
            wiki.addPage(item.uri, item.markdown);
        }
        const uri = window.location.pathname.substring(1);
        const page = wiki.getPage(uri);
        showPage(page);
        sidebar.update(wiki.getPages());
    });

    wiki.onChange(() => sidebar.update(wiki.getPages()));

    return fragmentOf([
        sidebar.el,
        htmlBuilder('main').withChildren([pageView.rootElement, additionalPagesView.rootElement]).build(),
        notifications.el,
        editor.mirror,
    ]);
}
