import { htmlBuilder } from '../html';

interface Notifications {
    el: HTMLElement;
    add: (message: string, type: 'success' | 'error') => void;
}
export function Notifications(): Notifications {
    const el = htmlBuilder('div').addClass('notifications').build();
    return {
        el,
        add(message: string, type: 'success' | 'error'): void {
            const notification = htmlBuilder('div')
                .addClass('notification', type === 'success' ? 'notification--success' : 'notification--error')
                .innerText(message)
                .build();
            el.appendChild(notification);
            setTimeout(() => el.removeChild(notification), 3000);
        },
    };
}
