import { Api } from './Api';

export default class Upload {
    private readonly allowedMimeTypes = ['image/png', 'image/jpeg', 'application/pdf'];
    constructor(private readonly api: Api) {}

    public upload(dataTransfer: DataTransfer, callback: (uri: string) => void): void {
        for (const item of dataTransfer.items ?? []) {
            if (item.kind === 'file' && this.allowedMimeTypes.includes(item.type)) {
                const file = item.getAsFile();
                if (file) {
                    this.api.uploadFile(file).then((uploadedFile) => callback(uploadedFile.filename));
                }
            }
        }
    }
}
