import { Page } from './Model/Page';

interface ApiPage {
    uri: string;
    markdown: string;
}
type ApiPages = ApiPage[];

type UploadedFile = { filename: string };
type SaveResult = { success: boolean };
type DeleteResult = { success: boolean };

export class Api {
    private readonly uri: string;
    constructor(uri: string) {
        this.uri = uri;
    }

    loadPages(): Promise<ApiPages> {
        return fetch(this.uri + '/pages')
            .then((r) => r.json())
            .then((json: { data: ApiPages }) => json.data);
    }

    savePage(page: Page): Promise<SaveResult> {
        return fetch(this.uri + '/pages/' + page.getUri(), {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                uri: page.getUri(),
                markdown: page.toMarkdown(),
            }),
        })
            .then((res) => res.json())
            .then((json: SaveResult) => json);
    }

    deletePage(page: Page): Promise<DeleteResult> {
        return fetch(this.uri + '/pages/' + page.getUri(), {
            method: 'DELETE',
            headers: {
                'Content-Type': 'application/json',
            },
        })
            .then((res) => res.json())
            .then((json: DeleteResult) => json);
    }

    uploadFile(file: File): Promise<UploadedFile> {
        const formData = new FormData();
        formData.append('file', file);
        return fetch(this.uri + '/files', {
            method: 'POST',
            body: formData,
        })
            .then((res: Response) => res.json())
            .then((json: { data: UploadedFile }) => json.data);
    }
}
