import { htmlBuilder, replaceContent } from '../html';
import Markdown from '../Markdown';
import { Block } from '../Model/Block';
import Upload from '../Upload';
import { PageList } from '../Model/Wiki';

type Position = number;

type CursorPosition = 'start' | 'between' | 'end';

export interface EditorProps {
    markdown: Markdown;
    upload: Upload;
    pageList: PageList;
    blockElementMap: Map<Block, HTMLElement>;
    renderBlock: (block: Block) => HTMLElement;
}

export interface Editor {
    editor: HTMLTextAreaElement;
    mirror: HTMLTextAreaElement;
    isEditing: (block: Block) => boolean;
    startEditing: (block: Block, position: Position) => void;
    stopEditing: () => void;
}

interface AutoComplete {
    element: HTMLElement;
    focus: () => void;
}
function makeAutoComplete(pageList: PageList, confirm: (uri: string) => void, onClose: () => void): AutoComplete {
    let selectedElement: HTMLElement | undefined;
    let valueOnKeydown = '';

    const rootElement = htmlBuilder('div').addClass('autocomplete').build();
    const listElement = htmlBuilder('ul').addClass('autocomplete__list').build();
    const inputElement = htmlBuilder('input')
        .addClass('autocomplete__input')
        .addEventListener('keyup', (ev) => {
            if (ev.key === 'Enter') {
                confirm(selectedElement?.dataset.uri || '');
                rootElement.parentElement?.removeChild(rootElement);
                onClose();
                return;
            }
            if (ev.key === 'Escape') {
                // this.editor?.autocompleteCancel();
                rootElement.parentElement?.removeChild(rootElement);
                onClose();
                return;
            }
            if (valueOnKeydown !== inputElement.value) {
                const items: string[] = [];
                for (const result of pageList.filterPagesByTitle(inputElement.value).slice(0, 20)) {
                    items.push(
                        `<li data-uri="${result.getUri()}" class="autocomplete__item">${result.getTitle()}</li>`,
                    );
                }
                listElement.innerHTML = items.join('');
                if (listElement.firstElementChild instanceof HTMLLIElement) {
                    selectedElement = listElement.firstElementChild;
                    selectedElement.classList.add('autocomplete__item--selected');
                }
            }
        })
        .addEventListener('keydown', (ev) => {
            valueOnKeydown = inputElement.value;
            if (ev.key === 'ArrowDown' && selectedElement?.nextElementSibling instanceof HTMLLIElement) {
                ev.preventDefault();
                selectedElement.classList.remove('autocomplete__item--selected');
                selectedElement = selectedElement.nextElementSibling;
                selectedElement.classList.add('autocomplete__item--selected');
                return;
            }
            if (ev.key === 'ArrowUp' && selectedElement?.previousElementSibling instanceof HTMLLIElement) {
                ev.preventDefault();
                selectedElement.classList.remove('autocomplete__item--selected');
                selectedElement = selectedElement.previousElementSibling;
                selectedElement.classList.add('autocomplete__item--selected');
                return;
            }
        })
        .build();

    rootElement.appendChild(inputElement);
    rootElement.appendChild(listElement);
    return {
        element: rootElement,
        focus: () => inputElement.focus(),
    };
}

export function Editor({ markdown, upload, blockElementMap, renderBlock, pageList }: EditorProps): Editor {
    const editor = htmlBuilder('textarea')
        .addClass('inline-editor')
        .setAttribute('placeholder', 'No content')
        .setAttribute('rows', '1')
        .build();
    const mirror = htmlBuilder('textarea')
        .addClass('inline-editor', 'inline-editor--mirror')
        .setAttribute('placeholder', 'No content')
        .setAttribute('rows', '1')
        .build();
    let editing: [Block, HTMLElement] | undefined;

    // https://stackoverflow.com/a/25621277
    function autoResize(): void {
        editor.style.height = 'auto';
        editor.style.height = editor.scrollHeight + 'px';
    }

    function stopEditing(): void {
        if (editing) {
            const [block, element] = editing;
            editing = undefined;
            const editorValue = editor.value;
            block.content = editorValue;
            element.classList.remove('block--editing');
            if (block.isEmpty()) {
                element.classList.add('block--empty');
            } else {
                element.classList.remove('block--empty');
            }
            element.innerHTML = markdown.render(block.content);
        }
    }

    function startEditing(block: Block, position: Position): void {
        const element = blockElementMap.get(block);
        if (element) {
            stopEditing();
            editing = [block, element];
            element.classList.add('block--editing');
            editor.value = block.content;
            replaceContent(element, editor);
            editor.setSelectionRange(position, position);
            editor.focus();
            autoResize();
        }
    }

    function split(block: Block, ev: KeyboardEvent): void {
        if (ev.shiftKey || ev.ctrlKey) {
            return;
        }
        ev.preventDefault();
        const before = editor.value.substring(0, editor.selectionStart);
        const after = editor.value.substring(editor.selectionEnd);
        editor.value = before;
        stopEditing();
        const newBlock = block.split(before, after);
        const newElement = renderBlock(newBlock);
        blockElementMap.set(newBlock, newElement);
        const oldElement = blockElementMap.get(block);
        if (oldElement) {
            oldElement.insertAdjacentElement('afterend', newElement);
        }
        startEditing(newBlock, 0);
    }

    function cursorPosition(): CursorPosition {
        if (editor.selectionStart === 0 && editor.selectionEnd === 0) {
            return 'start';
        }
        if (editor.selectionStart === editor.value.length && editor.selectionEnd === editor.value.length) {
            return 'end';
        }
        return 'between';
    }

    function mergeWithPrev(block: Block, ev: KeyboardEvent): void {
        if ((cursorPosition() !== 'start' && editor.value.length > 0) || !block.prev) {
            return;
        }
        ev.preventDefault();
        stopEditing();
        const prevBlock = block.prev;
        const nextPosition = prevBlock.content.length;
        const removedBlock = block.mergeWithPrev();
        if (removedBlock) {
            blockElementMap.get(removedBlock)?.remove();
        }
        const component = blockElementMap.get(prevBlock);
        if (component) {
            startEditing(prevBlock, nextPosition);
        }
    }

    function mergeWithNext(block: Block, ev: KeyboardEvent): void {
        if ((cursorPosition() !== 'end' && editor.value.length > 0) || !block.next) {
            return;
        }
        ev.preventDefault();
        stopEditing();
        const nextPosition = block.content.length;
        const removedBlock = block.mergeWithNext();
        if (removedBlock) {
            blockElementMap.get(removedBlock)?.remove();
        }
        startEditing(block, nextPosition);
    }

    function changeLevel(block: Block, element: HTMLElement, ev: KeyboardEvent): void {
        if (!block.prev) {
            return;
        }
        ev.preventDefault();
        const newLevel = block.changeLevel(ev.shiftKey ? -1 : 1);
        element.dataset.level = newLevel.toString();
    }

    function isInFirstLine(): boolean {
        mirror.style.width = editor.clientWidth + 'px';
        mirror.innerHTML = 'a';
        const oneLineHeight = mirror.scrollHeight;
        mirror.innerHTML = editor.value.substring(0, editor.selectionStart);
        return mirror.scrollHeight === oneLineHeight;
    }

    function isInLastLine(): boolean {
        mirror.style.width = editor.clientWidth + 'px';
        mirror.innerHTML = editor.value;
        const fullHeight = mirror.scrollHeight;
        mirror.innerHTML = editor.value.substring(0, editor.selectionEnd);
        return mirror.scrollHeight === fullHeight;
    }

    function navigate(block: Block, ev: KeyboardEvent): void {
        if (ev.key === 'ArrowUp' && block.prev && isInFirstLine()) {
            ev.preventDefault();
            startEditing(block.prev, -1);
        } else if (ev.key === 'ArrowDown' && block.next && isInLastLine()) {
            ev.preventDefault();
            startEditing(block.next, -1);
        } else if (ev.key === 'ArrowLeft' && cursorPosition() === 'start' && block.prev) {
            ev.preventDefault();
            startEditing(block.prev, -1);
        } else if (ev.key === 'ArrowRight' && cursorPosition() === 'end' && block.next) {
            ev.preventDefault();
            startEditing(block.next, 0);
        }
    }

    editor.addEventListener('keydown', (ev) => {
        if (!editing) {
            return;
        }
        const [block, element] = editing;
        if (ev.key === 'Enter' && !ev.shiftKey) {
            ev.preventDefault();
            split(editing[0], ev);
        } else if (ev.key === 'Escape' || (ev.key === 's' && ev.ctrlKey)) {
            ev.preventDefault();
            stopEditing();
        } else if (ev.key === 'Backspace') {
            mergeWithPrev(block, ev);
        } else if (ev.key === 'Delete') {
            mergeWithNext(block, ev);
        } else if (ev.key === 'Tab') {
            changeLevel(block, element, ev);
        } else if (['ArrowUp', 'ArrowDown', 'ArrowLeft', 'ArrowRight'].includes(ev.key)) {
            navigate(block, ev);
        }
    });

    editor.addEventListener('input', () => {
        autoResize();
    });

    editor.addEventListener('keyup', (ev) => {
        if (ev.key === ']' && editor.value.substring(editor.selectionStart - 2, editor.selectionStart-1) === '[') {
            const autoComplete = makeAutoComplete(
                pageList,
                (uri) => {
                    editor.value = editor.value + ' [](./' + uri + ')';
                },
                () => editor.focus(),
            );
            editor.parentElement?.appendChild(autoComplete.element);
            autoComplete.focus();
        }
    });

    editor.addEventListener('paste', (ev: ClipboardEvent) => {
        if (ev.clipboardData) {
            upload.upload(ev.clipboardData, (uri) => {
                editor.value = editor.value + '![](./' + uri + ')';
            });
        }
    });

    document.addEventListener('drop', (ev: DragEvent) => {
        if (ev.type === 'drop' && ev.target === editor && ev.dataTransfer) {
            ev.preventDefault();
            upload.upload(ev.dataTransfer, (uri) => {
                editor.value = editor.value + '![](./' + uri + ')';
            });
        }
    });

    document.addEventListener('click', (ev: MouseEvent) => {
        if (ev.target instanceof HTMLElement && !ev.target.closest('.page')) {
            stopEditing();
        }
    });

    return {
        editor,
        mirror,
        isEditing(block): boolean {
            return editing ? editing[0] === block : false;
        },
        startEditing,
        stopEditing,
    };
}
