import { Wiki } from './Wiki';
import { Block } from './Block';

type PageUri = string;

export interface InternalLink {
    from: PageUri;
    to: PageUri;
    linkText: string;
    block: Block;
}

export interface SearchMatch {
    before: string;
    match: string;
    after: string;
}

export interface SearchMatches {
    score: number;
    matches: SearchMatch[];
}

export class Page {
    private uri: string;
    private title: string;
    private firstBlock: Block;

    constructor(private readonly wiki: Wiki, uri: string, title: string, content: string, public exists: boolean) {
        this.uri = uri;
        this.title = decodeURI(title);
        this.firstBlock = this.parseContentToBlocks(content) ?? new Block(this, '', 0);
        this.undoStack.push(this.toMarkdown());
    }

    public static fromMarkdown(wiki: Wiki, uri: string, markdown: string): Page {
        const [title, content] = Page.splitMarkdown(markdown);
        return new Page(wiki, uri, title, content, true);
    }

    private static splitMarkdown(markdown: string): [string, string] {
        const lines = markdown.split('\n');
        lines.shift();
        const title = (lines.shift() ?? '').substring('title: '.length).replace(/^"(.*)"$/, '$1');
        lines.shift();
        while (lines[0] === '') {
            lines.shift();
        }
        return [title, lines.join('\n')];
    }

    public getTitle(): string {
        return this.title;
    }

    public setTitle(newTitle: string): void {
        if (newTitle !== this.title) {
            this.title = newTitle;
            this.scheduleChangeListenerCall();
        }
    }

    public setMarkdown(markdown: string): void {
        const [title, content] = Page.splitMarkdown(markdown);
        if (!this.isDate()) {
            this.title = title;
        }
        this.firstBlock = this.parseContentToBlocks(content) ?? new Block(this, '', 0);
        this.scheduleChangeListenerCall();
    }

    public getUri(): string {
        return this.uri;
    }

    public getBacklinks(): InternalLink[] {
        return this.wiki.getAllLinksTo(this);
    }

    public matches(query: string): SearchMatches {
        let score = this.title.toLowerCase().indexOf(query.toLowerCase()) >= 0 ? 100 : 0;
        const context = 20;
        const matches: SearchMatch[] = [];
        for (const block of this.blocksInOrder()) {
            const content = block.content;
            let idx = content.toLowerCase().indexOf(query.toLowerCase());
            while (idx >= 0) {
                score += 1;
                matches.push({
                    before: content.substring(Math.max(0, idx - context), idx),
                    match: content.substring(idx, idx + query.length),
                    after: content.substring(idx + query.length, idx + query.length + context),
                });
                idx = content.toLowerCase().indexOf(query.toLowerCase(), idx + 1);
            }
        }
        return { score, matches };
    }

    public links(): InternalLink[] {
        return this.blocksInOrder().flatMap((block) => block.internalLinks());
    }

    public blocksInOrder(): Block[] {
        const result = [];
        let cur: Block | undefined = this.firstBlock;
        while (cur) {
            result.push(cur);
            cur = cur.next;
        }
        return result;
    }

    public toMarkdown(): string {
        const content: string[] = ['---', 'title: "' + this.getTitle().trim() + '"', '...', ''];
        for (const block of this.blocksInOrder()) {
            const lines = block.content.split('\n');
            content.push(' '.repeat(block.level * 4) + '* ' + lines.shift());
            content.push(...lines.map((line) => ' '.repeat(block.level * 4 + 2) + line));
        }
        content.push('');
        return content.join('\n');
    }

    public isDate(): boolean {
        return this.title.match(/\d{4}-\d{2}-\d{2}/) !== null;
    }

    public prev(wiki: Wiki): Page | undefined {
        if (this.isDate()) {
            const date = new Date(this.title);
            date.setDate(date.getDate() - 1);
            return wiki.getPage(date.toISOString().substring(0, 10));
        }
    }

    public onBlockChange(): void {
        this.scheduleChangeListenerCall();
    }

    private saving = false;
    private scheduleChangeListenerCall(): void {
        this.saving = true;
        setTimeout(() => {
            if (this.saving) {
                this.saving = false;
                this.callChangeListeners();
            }
        }, 0);
    }

    public addChangeListener(listener: () => void): void {
        this.changeListeners.push(listener);
    }

    public removeChangeListener(listener: () => void): void {
        this.changeListeners.splice(this.changeListeners.indexOf(listener), 1);
    }

    private undoStack: string[] = [];
    private redoStack: string[] = [];
    private changeListeners: (() => void)[] = [];
    private callChangeListeners(): void {
        for (const listener of this.changeListeners) {
            listener();
        }
        this.pushUnique(this.undoStack, this.toMarkdown());
    }

    public undo(): void {
        const current = this.undoStack.pop();
        if (!current) {
            return;
        }
        this.pushUnique(this.redoStack, current);
        const prev = this.undoStack.pop();
        if (prev) {
            this.setMarkdown(prev);
        } else {
            this.redoStack.pop();
            this.undoStack.push(current);
        }
    }

    private pushUnique<T>(stack: T[], item: T): void {
        if (stack[stack.length - 1] !== item) {
            stack.push(item);
        }
    }

    public redo(): void {
        const next = this.redoStack.pop();
        if (next) {
            this.pushUnique(this.undoStack, next);
            this.setMarkdown(next);
        }
    }

    private parseContentToBlocks(pageContent: string): Block | undefined {
        const lines = pageContent.split('\n');
        const regex = new RegExp(/^( *)\* (.*)$/);
        let firstBlock: Block | undefined;
        let lastBlock: Block | undefined;
        const indentations: number[] = [];
        const content: string[] = [];
        // Remove empty lines between frontmatter and first block
        while (lines[0] === '') {
            lines.shift();
        }

        const finalize = (): void => {
            if (content.length < 1) {
                return;
            }
            const newBlock = new Block(this, content.join('\n'), Math.max(0, indentations.length - 1));
            content.splice(0);
            if (!firstBlock) {
                firstBlock = newBlock;
            }
            if (lastBlock) {
                lastBlock.next = newBlock;
                newBlock.prev = lastBlock;
            }
            lastBlock = newBlock;
        };

        for (const line of lines) {
            const match = line.match(regex);
            if (match) {
                finalize();
                const indentation = match[1].length;
                while (indentations[indentations.length - 1] >= indentation) {
                    indentations.pop();
                }
                indentations.push(indentation);
            }
            content.push(line.substr(indentations[indentations.length - 1] + 2));
        }
        finalize();
        return firstBlock;
    }
}
