import { htmlBuilder, fragmentOf, replaceContent, internalLinkBuilder } from '../html';
import { Page, InternalLink } from '../Model/Page';
import Markdown from '../Markdown';
import Navigation from '../Navigation';
import { PageHeader, PageHeaderProps } from './PageHeader';
import { Block } from '../Model/Block';

export interface BlockProps {
    markdown: Markdown;
    registerBlockElement: (block: Block, element: HTMLElement) => void;
    onBlockClick: (block: Block, event: MouseEvent) => void;
}

interface BacklinkProps {
    markdown: Markdown;
    navigation: Navigation;
}

type BlockModeProps = PageHeaderProps & BlockProps & BacklinkProps;

interface RawModeProps {
    setMode: (page: Page, mode: PageViewMode) => void;
}

export type PageViewMode = 'raw' | 'block';

export interface PageViewState {
    page: Page;
    mode: PageViewMode;
}

export interface PageViewProps {
    navigation: Navigation;
    markdown: Markdown;
    registerBlockElement: (block: Block, element: HTMLElement) => void;
    onBlockClick: (block: Block, event: MouseEvent) => void;
    deletePage: (page: Page) => void;
}

interface PageView {
    rootElement: HTMLDivElement;
    update: (newState: PageViewState) => void;
}

export function makeBlock({ markdown, registerBlockElement, onBlockClick }: BlockProps, block: Block): HTMLElement {
    return htmlBuilder('div')
        .addClass('block', block.isEmpty() ? 'block--empty' : false)
        .data('level', block.level.toString())
        .innerHTML(markdown.render(block.content))
        .apply((el) => registerBlockElement(block, el))
        .addEventListener('click', (ev) => onBlockClick(block, ev))
        .build();
}

function makeBacklink({ markdown, navigation }: BacklinkProps, link: InternalLink): DocumentFragment {
    const pageBlock = htmlBuilder('div')
        .addClass('block')
        .data('level', '0')
        .appendChild(internalLinkBuilder(navigation, link.from).innerText(link.linkText).build())
        .build();

    const blocks = link.block.parents();
    const children = link.block.children();
    if (children.length === 0) {
        blocks.push(...(link.block.parent()?.children() ?? []));
    } else {
        blocks.push(link.block);
        blocks.push(...children);
    }
    return fragmentOf([
        pageBlock,
        ...blocks.map((block) =>
            htmlBuilder('div')
                .addClass('block')
                .data('level', (block.level + 1).toString())
                .innerHTML(markdown.render(block.content))
                .build(),
        ),
    ]);
}

function renderBlockMode(props: BlockModeProps, page: Page): DocumentFragment {
    return fragmentOf([
        PageHeader(props, page),
        htmlBuilder('div')
            .addClass('blocks')
            .withChildren(page.blocksInOrder().map((block) => makeBlock(props, block)))
            .build(),
        htmlBuilder('h2').innerText('Backlinks').build(),
        htmlBuilder('div')
            .addClass('blocks', 'backlinks')
            .withChildren(page.getBacklinks().flatMap((link) => makeBacklink(props, link)))
            .build(),
    ]);
}

function renderRawMode(props: RawModeProps, page: Page): DocumentFragment {
    const textarea = htmlBuilder('textarea')
        .addClass('raw-editor')
        .innerHTML(page.toMarkdown())
        .addEventListener('keydown', (ev, el) => {
            if (ev.key === 's' && ev.ctrlKey) {
                ev.preventDefault();
                page.setMarkdown(el.value);
                props.setMode(page, 'block');
            }
        })
        .build();
    return fragmentOf([
        textarea,
        htmlBuilder('div')
            .addClass('buttons')
            .withChildren([
                htmlBuilder('button')
                    .innerText('save')
                    .addEventListener('click', () => {
                        page.setMarkdown(textarea.value);
                        props.setMode(page, 'block');
                    })
                    .build(),
                htmlBuilder('button')
                    .innerText('close')
                    .addEventListener('click', () => {
                        props.setMode(page, 'block');
                    })
                    .build(),
            ])
            .build(),
    ]);
}

export function PageView(props: PageViewProps, initialState?: PageViewState): PageView {
    const rootElement = htmlBuilder('div').addClass('page').build();
    let oldState: PageViewState | undefined;

    const blockModeProps: BlockModeProps = {
        ...props,
        setMode: (page, mode) => update({ page, mode }),
    };

    function update(newState: PageViewState): void {
        oldState = newState;
        document.title = newState.page.getTitle();

        if (newState.mode === 'raw') {
            replaceContent(
                rootElement,
                renderRawMode({ setMode: (page, mode) => update({ page, mode }) }, newState.page),
            );
            rootElement.classList.add('page--raw');
        } else {
            replaceContent(rootElement, renderBlockMode(blockModeProps, newState.page));
            rootElement.classList.remove('page--raw');
        }

        window.scrollTo(0, 0);
    }

    if (initialState) {
        update(initialState);
    }

    return {
        rootElement,
        update,
    };
}
