import { Page, SearchMatches } from '../Model/Page';
import Navigation from '../Navigation';
import Icons from './Icons';
import { htmlBuilder, replaceContent, fragmentOf, internalLinkBuilder } from '../html';

type PageWithMatches = [Page, SearchMatches];

function comparePagesWithMatches(a: PageWithMatches, b: PageWithMatches): number {
    if (a[1].score === b[1].score) {
        if (a[0].isDate() && b[0].isDate()) {
            return b[0].getTitle().localeCompare(a[0].getTitle());
        }
        if (a[0].isDate() || b[0].isDate()) {
            return b[0].isDate() ? (a[0].isDate() ? 0 : 1) : -1;
        }
        return a[0].getTitle().localeCompare(b[0].getTitle());
    } else {
        return b[1].score - a[1].score;
    }
}

function makeResultItemElement(navigation: Navigation, [page, matches]: PageWithMatches): HTMLLIElement {
    return htmlBuilder('li')
        .appendChild(
            internalLinkBuilder(navigation, page.getUri())
                .withChildren([
                    htmlBuilder('span').innerText(page.getTitle()).build(),
                    ...matches.matches.map((match) =>
                        htmlBuilder('span')
                            .withChildren([
                                document.createTextNode(match.before),
                                htmlBuilder('b').innerText(match.match).build(),
                                document.createTextNode(match.after),
                            ])
                            .build(),
                    ),
                ])
                .build(),
        )
        .build();
}

function makeFallbackResult(navigation: Navigation, query: string): HTMLLIElement {
    return htmlBuilder('li')
        .addClass('create')
        .appendChild(
            internalLinkBuilder(navigation, query)
                .innerText('Create page "' + query + '"')
                .build(),
        )
        .build();
}

function makeInputElement(
    search: (query: string) => void,
    withSelected: (fn: (selected: HTMLElement) => void) => void,
    select: (newSelection: HTMLLIElement) => void,
    initialValue: string,
): HTMLInputElement {
    const inputElement = document.createElement('input');
    inputElement.value = initialValue;
    inputElement.setAttribute('type', 'search');
    inputElement.setAttribute('placeholder', 'Search');
    inputElement.addEventListener('input', () => {
        search(inputElement.value);
    });
    inputElement.addEventListener('keydown', (ev) => {
        if (ev.key === 'Escape') {
            inputElement.value = '';
            search('');
        } else {
            withSelected((selected) => {
                const firstChild = selected?.firstElementChild;
                if (ev.key === 'Enter' && firstChild instanceof HTMLAnchorElement) {
                    ev.preventDefault();
                    firstChild.click();
                } else if (ev.key === 'ArrowDown') {
                    ev.preventDefault();
                    const nextSibling = selected.nextElementSibling;
                    if (nextSibling instanceof HTMLLIElement) {
                        select(nextSibling);
                    }
                } else if (ev.key === 'ArrowUp') {
                    ev.preventDefault();
                    const prevSibling = selected.previousElementSibling;
                    if (prevSibling instanceof HTMLLIElement) {
                        select(prevSibling);
                    }
                }
            });
        }
    });
    return inputElement;
}

export interface Sidebar {
    el: HTMLElement;
    update: (pages: Page[]) => void;
}
export function Sidebar(navigation: Navigation, initialPages: Page[]): Sidebar {
    const listElement = document.createElement('ul');
    let query = localStorage.getItem('lastSearch') ?? '';
    let selected: HTMLElement | undefined;
    let pages: Page[] = [];
    const inputElement = makeInputElement(search, (fn) => (selected ? fn(selected) : {}), select, query);

    function select(el: HTMLElement): void {
        if (selected) {
            selected.classList.remove('active');
        }
        selected = el;
        selected.classList.add('active');
    }

    function search(newQuery: string): void {
        query = newQuery;
        if (query.length === 1) {
            return;
        }
        localStorage.setItem('lastSearch', query);
        const results = pages
            .map((page): PageWithMatches => [page, query === '' ? { matches: [], score: 1 } : page.matches(query)])
            .filter(([, { score }]) => score > 0)
            .sort(comparePagesWithMatches)
            .map((item) => makeResultItemElement(navigation, item));
        if (results.length > 0) {
            replaceContent(listElement, fragmentOf(results));
            select(results[0]);
        } else {
            const createOffer = makeFallbackResult(navigation, query);
            replaceContent(listElement, createOffer);
            select(createOffer);
        }
    }

    function update(newPages: Page[]): void {
        pages = newPages;
        for (const page of pages) {
            page.addChangeListener(() => {
                search(query);
            });
        }
        search(query);
    }

    window.addEventListener('keyup', (e) => {
        if (e.key === 'f' && document.activeElement === document.body) {
            inputElement.focus();
            inputElement.select();
            return;
        }
    });

    if (initialPages) {
        update(initialPages);
    }

    return {
        el: htmlBuilder('div')
            .addClass('sidebar')
            .withChildren([
                htmlBuilder('form')
                    .withChildren([internalLinkBuilder(navigation, '/').innerHTML(Icons.home).build(), inputElement])
                    .build(),
                listElement,
            ])
            .build(),
        update,
    };
}
