import { htmlBuilder, internalLinkBuilder } from '../html';
import { Page } from '../Model/Page';
import Navigation from '../Navigation';
import { dateToString } from '../util';
import Icons from './Icons';
import { PageViewMode } from './PageView';

export interface PageHeaderProps {
    navigation: Navigation;
    setMode: (page: Page, mode: PageViewMode) => void;
    deletePage: (page: Page) => void;
}

function PageTitle(navigation: Navigation, page: Page): HTMLElement {
    if (page.isDate()) {
        return htmlBuilder('h1')
            .appendChild(internalLinkBuilder(navigation, page.getUri()).innerText(page.getTitle()).build())
            .build();
    } else {
        return htmlBuilder('h1')
            .appendChild(
                htmlBuilder('input')
                    .apply((el) => (el.value = page.getTitle()))
                    .addEventListener('keydown', (ev, el) => {
                        if ((ev.key === 's' && ev.ctrlKey) || ev.key === 'Enter') {
                            ev.preventDefault();
                            el.blur();
                        }
                    })
                    .addEventListener('change', (_ev, el) => page.setTitle(el.value))
                    .addEventListener('input', (_ev, el) => page.setTitle(el.value))
                    .build(),
            )
            .build();
    }
}

function DateNavigation(navigation: Navigation, page: Page): HTMLElement | undefined {
    if (page.isDate()) {
        const prevDate = new Date(page.getTitle());
        prevDate.setDate(prevDate.getDate() - 1);
        const nextDate = new Date(page.getTitle());
        nextDate.setDate(nextDate.getDate() + 1);
        return htmlBuilder('div')
            .addClass('date-nav')
            .withChildren([
                internalLinkBuilder(navigation, dateToString(prevDate))
                    .innerHTML(Icons.chevronLeft)
                    .addClass('date-link')
                    .build(),
                internalLinkBuilder(navigation, dateToString(nextDate))
                    .innerHTML(Icons.chevronRight)
                    .addClass('date-link')
                    .build(),
            ])
            .build();
    }
}

function PageActions(props: PageHeaderProps, page: Page): HTMLElement {
    let isOpen = false;
    const container = htmlBuilder('div').addClass('page__actions').build();
    const openButton = htmlBuilder('button')
        .innerHTML(Icons.dotsVertical)
        .addEventListener('click', () => {
            if (isOpen) {
                isOpen = false;
                container.classList.remove('page__actions--open');
            } else {
                isOpen = true;
                container.classList.add('page__actions--open');
            }
        })
        .build();
    const actionList = htmlBuilder('div')
        .addClass('page__action-list')
        .withChildren([
            htmlBuilder('button')
                .innerText('delete')
                .addEventListener('click', () => {
                    props.deletePage(page);
                })
                .build(),
            htmlBuilder('button')
                .innerText('edit raw')
                .addEventListener('click', () => {
                    container.classList.remove('page__actions--open');
                    props.setMode(page, 'raw');
                })
                .build(),
        ])
        .build();

    container.appendChild(openButton);
    container.appendChild(actionList);

    return container;
}

export function PageHeader(props: PageHeaderProps, page: Page): HTMLElement {
    return htmlBuilder('div')
        .addClass('page__header')
        .withChildren([
            PageTitle(props.navigation, page),
            DateNavigation(props.navigation, page),
            PageActions(props, page),
        ])
        .build();
}
